import {debounce} from "lodash";
import Avatar from "@/components/Avatar";
import BInput from "@/components/input/Input";

export default {
    name: "PostSelector",
    components: {Avatar, BInput},
    props: {
        label: String,
        query: {
            default: () => {
                return {}
            },
            type: Object
        },
        value: {},
        type: {
            default: 'plant',
            type: String
        },
        disabled: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            response: {
                results: [],
                count: 0
            },
            page: 1
        }
    },
    methods: {
        async fetch(text) {
            this.response = await this.$axios.$get('/auth/users/', {
                search: text,
                ...this.query,
                page_size: 5,
                type: this.type,
                page: this.page
            })
        },
        handle_input: debounce(function (text) {
            if (!this.disabled) {
                this.fetch(text);
            }
        }, 800),
        select(p) {
            if (this.disabled) return;
            let data = p;
            if (this.value && p.id === this.value.id) {
                data = null;
            }
            this.$emit('input', data);
        }
    },
    watch: {
        "query.taxonomies": {
            deep: true,
            handler: function () {
                this.page = 1;
                this.fetch(null);
            }
        }
    }
}
