export default {
    name: 'BRate',
    props: {
        value: {
            type: Number,
            default: 0
        },
        max: {
            type: Number,
            default: 5
        },
        icon: {
            type: String,
            default: 'star'
        },
        iconPack: String,
        size: String,
        spaced: Boolean,
        rtl: Boolean,
        disabled: Boolean,
        showScore: Boolean,
        showText: Boolean,
        customText: String,
        texts: Array
    },
    data() {
        return {
            newValue: this.value,
            hoverValue: 0
        }
    },
    computed: {
        halfStyle() {
            return `width:${this.valueDecimal}%`
        },
        showMe() {
            let result = ''
            if (this.showScore) {
                result = this.disabled ? this.value : this.newValue
                if (result === 0) result = ''
            } else if (this.showText) {
                result = this.texts[Math.ceil(this.newValue) - 1]
            }
            return result
        },
        valueDecimal() {
            return this.value * 100 - Math.floor(this.value) * 100
        }
    },
    watch: {
        // When v-model is changed set the new value.
        value(value) {
            this.newValue = value
        }
    },
    methods: {
        resetNewValue() {
            if (this.disabled) return
            this.hoverValue = 0
        },
        previewRate(index, event) {
            if (this.disabled) return
            this.hoverValue = index
            event.stopPropagation()
        },
        confirmValue(index) {
            if (this.disabled) return
            this.newValue = index
            this.$emit('change', this.newValue)
            this.$emit('input', this.newValue)
        },
        checkHalf(index) {
            let showWhenDisabled = this.disabled && this.valueDecimal > 0 &&
            index - 1 < this.value && index > this.value
            return showWhenDisabled
        },
        rateClass(index) {
            let output = ''
            const currentValue = this.hoverValue !== 0 ? this.hoverValue : this.newValue
            if (index <= currentValue) {
                output = 'set-on'
            } else if (this.disabled && (Math.ceil(this.value) === index)) {
                output = 'set-half'
            }
            return output
        }
    }
}
