import {Task} from "../plugins/task";

export default {
    name: "Task",
    props: {
        value: {
            type: Object,
            default: () => {
                return {}
            }
        }
    },
    data() {
        return {
            task: this.value,
        }
    },
    computed: {
        style() {
            if (this.task.temp_setting) {
                return {
                    top: this.task.temp_setting.top + 'px',
                    left: this.task.temp_setting.left + 'px'
                }
            }
            return {}
        },
    },
    watch: {
        value() {
            this.task = this.value
        },
        task: {
            deep: true,
            handler: function () {
                this.$emit('input', this.task);
            }
        },
    },
    methods: {
        handle(data) {
            this.$emit('down', data);
        },
        async task_add() {
            let st = this.$store.state.config.settings.timer;
            let task = new Task({
                tomato: st.tomato,
                updating: true,
                parent: this.task.id !== 0 ? this.task.id : undefined,
                board: this.task.board
            });
            this.$emit('add', task);
        },
        async handle_add(task) {
            this.$emit('add', task);
        },
    }
}
