import config from '../utils/config'

export default {
    name: 'BTooltip',
    props: {
        active: {
            type: Boolean,
            default: true
        },
        type: String,
        label: String,
        position: {
            type: String,
            default: 'is-top',
            validator(value) {
                return [
                    'is-top',
                    'is-bottom',
                    'is-left',
                    'is-right'
                ].indexOf(value) > -1
            }
        },
        always: Boolean,
        animated: Boolean,
        square: Boolean,
        dashed: Boolean,
        multilined: Boolean,
        size: {
            type: String,
            default: 'is-medium'
        },
        delay: Number
    },
    computed: {
        newType() {
            return this.type || config.defaultTooltipType
        },
        newAnimated() {
            return this.animated || config.defaultTooltipAnimated
        },
        newDelay() {
            return this.delay || config.defaultTooltipDelay
        }
    }
}
