import Upload from "./Upload";

export default {
    name: "Avatar",
    components: {
        Upload
    },
    props: {
        canUpdate: {type: Boolean, default: false},
        value: {
            default: null,
            type: Object
        },
        name: {
            default: null
        },
        icon: {
            default: 'account-circle-outline',
            type: String
        },
        imageSize: {
            default: 'thumb_96_96',
            type: String
        }
    },
    data() {
        return {
            data: this.value,
            isUpdating: false
        }
    },
    methods: {
        handleUpload(files) {
            if (files.length) {
                this.data = files[0]
                this.isUpdating = false;
            }
        },
        getRandomInt(max) {
            return Math.floor(Math.random() * Math.floor(max));
        }
    },
    watch: {
        data() {
            this.$emit('input', this.data)
        },
        value() {
            this.data = this.value
        }
    }
}
