import icons from './Icon.json';

export default {
    name: 'AppIcon',
    props: {
        name: String
    },
    computed: {
        path() {
            return typeof icons[this.name] === "string" ? icons[this.name] : icons[this.name].path;
        },
        transform() {
            return typeof icons[this.name] === "object" ? icons[this.name].transform : undefined;
        }
    },
};
